


























































































































































import { Component, Prop, Vue } from 'vue-property-decorator'
import { saveOrUpdateCourse, getCourseById } from '@/api/course'
import CourseImage from './CourseImage.vue'
import TextEditor from '@/components/TextEditor/index.vue'

import moment from 'moment'
@Component({
  components: {
    CourseImage,
    TextEditor
  }
})
export default class CreateOrUpdate extends Vue {
  @Prop({
    default: false
  })
  private isEdit: any

  @Prop()
  private courseId: any

  private activeStep = 0
  private steps: any[] = [
    { title: '基本信息', icon: 'el-icon-edit' },
    { title: '课程封面', icon: 'el-icon-edit' },
    { title: '销售信息', icon: 'el-icon-edit' },
    { title: '秒杀活动', icon: 'el-icon-edit' },
    { title: '课程详情', icon: 'el-icon-edit' }
  ]

  private course: any = {
    courseName: '',
    brief: '',
    teacherDTO: {
      teacherName: '',
      teacherHeadPicUrl: '',
      position: '',
      description: ''
    },
    courseDescriptionMarkDown: '',
    price: 0,
    discounts: 0,
    priceTag: '',
    discountsTag: '',
    isNew: true,
    isNewDes: '',
    courseListImg: '',
    courseImgUrl: '',
    sortNum: 0,
    previewFirstField: '',
    previewSecondField: '',
    status: 0, // 0：未发布，1：已发布
    sales: 0,
    activityCourse: false, // 是否开启活动秒杀
    activityCourseDTO: {
      beginTime: '',
      endTime: '',
      amount: 0,
      stock: 0
    },
    autoOnlineTime: ''
  }

  private created () {
    if (this.isEdit) {
      this.loadCourse()
    }
  }

  private async loadCourse () {
    const { data } = await getCourseById(this.courseId)
    const { activityCourseDTO } = data.data
    if (activityCourseDTO) {
      activityCourseDTO.beginTime = moment(activityCourseDTO.beginTime).format(
        'YYYY-MM-DD'
      )
      activityCourseDTO.endTime = moment(activityCourseDTO.endTime).format(
        'YYYY-MM-DD'
      )
    }
    this.course = data.data
  }

  private async handleSave () {
    const { data } = await saveOrUpdateCourse(this.course)
    if (data.code === '000000') {
      this.$message.success('保存成功')
      this.$router.push('/course')
    } else {
      this.$message.error('保存失败')
    }
  }
}
